export const range = (to) => [...new Array(to)].map((_, i) => Boolean(i));
export const createGrid = (size) => range(size)
    .fill(false)
    .map((_x, _i, row) => row.slice());
export const createRandomGrid = (size) => createGrid(size).map((row) => row.map(() => Math.random() >= 0.8));
export const calculateFrameRate = (ticks = 0, startedAt = Date.now(), now = Date.now()) => (startedAt ? Math.ceil(ticks / ((now - startedAt) / 1000)) : null);
