const newKey = (size) => (key) => {
    if (key === -1) {
        return size - 1;
    }
    if (key === size) {
        return 0;
    }
    return key;
};
const newKeys = (size, keys) => keys.map(newKey(size));
const combinePositions = ({ y, x }) => [-1, 0, 1].reduce((acc, $y, _, offset) => offset.reduce((b, $x) => ($x || $y ? [...b, [y + $y, x + $x]] : b), acc), []);
const getIn = (grid) => (position) => (([y, x]) => grid[y][x])(newKeys(grid.length, position));
export const getNeighbours = (grid, position) => combinePositions(position).map(getIn(grid)).filter(Boolean).length;
export const willLive = (isAlive, neighbours) => isAlive ? neighbours >= 2 && neighbours <= 3 : neighbours === 3;
export const nextState = (grid) => grid.map((row, y) => row.map((column, x) => willLive(Boolean(column), getNeighbours(grid, { y, x }))));
export const toggle = ({ y, x }, current, grid) => {
    const $grid = grid.slice();
    grid[y][x] = !current;
    return $grid;
};
